 */
 .coupon-form{
    width: 100%;
    position: relative;
    
}

.coupon-hide{
    display: none;
}
.coupon-show{
    display: block;
}

.coupon-btn{
    background-color: transparent;
    border: none;
    font-family: var(--ff-text);
    font-size: var(--fs-200);
    margin-left: 15px;
    position: relative;
    cursor: pointer;
    
}

.coupon-btn::before{
    content:'\f3ff';
    font-family: 'Font Awesome 5 Free';
    font-size: 0.8rem;
    font-weight: 900;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);

    position: absolute;
    top: 4px;
    left: -16px;
}

.coupon-form input{
    width: 100%;
    border: 1px solid black;
}

.coupon-form input::-webkit-input-placeholder{
    padding: 0 1rem;
}

.coupon-form input:-ms-input-placeholder{
    padding: 0 1rem;
}

.coupon-form input::placeholder{
    padding: 0 1rem;
}

.coupon-form button{
    background-color:var(--clr--pink-200);
    color: white;

    border: 1px solid black;
    border-left: none;
    cursor: pointer;

    
    position: absolute;
    top: 1px;
    right: 1px;
    bottom: 1px;
    
    padding: 0 .6rem;
}

.coupon-icon i{
    font-size: var(--fs-500);
}

.coupon-code-success{
    font-size: var(--fs-50);
    color: green;
    text-transform: capitalize;
    display: none;
}
.coupon-code-error{
    font-size: var(--fs-50);
    color: red;
    text-transform: capitalize;
    display: none;
}

