.complete-booking-container{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem 1rem;
}

.complete-booking-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap:2rem;
    gap:2rem;
}

.complete-booking-title{
    font-size: 1.8rem;
    font-family: var(--ff-title);
    font-weight: var(--fw-semi-bold);
    text-transform: capitalize;
    text-align:center
}

.complete-booking-subtitle{
    font-size: 1rem;
    font-weight: var(--fw-regular);  
    text-align:center 
}

.final-booking-info{
    border: 1px solid rgb(172, 170, 170);
    padding: 3rem 7rem 3rem 3rem;
