/* 
//////////////////////
    custom-properties
//////////////////////
*/

:root{
    --spacer: 1rem;

    --clr--pink-100: #FCD2C0; 
    --clr--pink-200: #FAB89D ; 
    --clr-black: #000000 ; 
    --clr-white: #FFFFFF ; 

    --ff-text:'Poppins', sans-serif; ; 
    --ff-header:'Playball', cursive; 
    --ff-title: 'Playfair Display', serif; 
    


    
    
    --fs-50: 0.75rem  ; /*12*/
    --fs-100: 0.875rem  ; /*14*/
    --fs-200: 0.937rem  ; /*15*/
    --fs-300: 1rem  ; /*16*/
    --fs-400: 1.0625rem  ; /*17*/
    --fs-500: 1.1875rem  ; /*19*/
    --fs-600: 1.375rem  ; /*22*/
    --fs-700: 1.75rem  ; /*28*/
    --fs-750: 1.875rem  ; /*30*/
    --fs-800: 1.75rem  ; /*43*/
    --fs-900: 3.75rem ; /*57*/
    --fs-950: 3.75rem ; /*60*/
    
    



    --fw-light:300 ;
    --fw-regular:400 ;
    --fw-med:500 ;
    --fw-semi-bold:600 ;
    --fw-bold:700 ;

    --m-size: 970px;
}

h1,h2,h3,h4,h5{
    line-height: 1.2;
}

ul[class] , li[class]{
    list-style: none;
    padding: 0;
}

a{
    color: black;
    text-decoration: none;
}

html{
    scroll-behavior: smooth;
}
body{
    font-size: 1.0625rem;
    font-size: var(--fs-400);
    font-family: 'Poppins', sans-serif;
    font-family: var(--ff-text);
    font-weight: 300;
    font-weight: var(--fw-light);
}



/*  
/////////////////
