}


.fancy-border{
    position: relative;
}

.fancy-border::after{
    content: '';
    width: 1.8rem;   
    height: 3px;
    position: absolute;
    top: -10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    
    background-color: var(--clr-black);
    
    background-color: var(--fancy-border , var(--clr-black));
}




