/*$ visited us background */
/* ///////////////////////////// */
.visit-us-container .row i{
    color: rgba(0,0,0,0.5);
    font-size: 64px;
    transition: 1s;
    padding: 0 5px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: default;
}

.visit-us-container .row i:hover{
    color: #0f0;
    text-shadow: 0 0 120px #0f0;
    transition: 0s;
}

.visit-us-container .row .clicked{
    color: #FAB89D;
    color: var(--clr--pink-200);
    text-shadow: 0 0 120px #FCD2C0;
    text-shadow: 0 0 120px var(--clr--pink-100);
    transition: 0s;

}

.visit-us-container .row-container {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    width: 100%;
    height: 800px;
    background: #111;
}

.visit-us-container .row-container .row {
    position: relative;
    top: -50%;
    width: 100%;
    display: flex;
    padding: 10px 0;
    white-space: nowrap;
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
   
}


.row-container .row div{
    -webkit-animation: animate1 80s linear infinite;
            animation: animate1 80s linear infinite;
    -webkit-animation-delay: -80s;
            animation-delay: -80s;
}


.row-container .row div:nth-child(2) {
    -webkit-animation: animate2 80s linear infinite;
            animation: animate2 80s linear infinite;
    -webkit-animation-delay: -40s;
            animation-delay: -40s;
}


@-webkit-keyframes animate1 {
    0% {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}


@keyframes animate1 {
    0% {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}


@-webkit-keyframes animate2 {
    0% {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
    100% {
        -webkit-transform: translateX(-200%);
                transform: translateX(-200%);
    }
}


@keyframes animate2 {
    0% {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
    100% {
        -webkit-transform: translateX(-200%);
                transform: translateX(-200%);
    }
}






.row-container .row:nth-child(even) div{
    -webkit-animation: animate3 80s linear infinite;
            animation: animate3 80s linear infinite;
    -webkit-animation-delay: -80s;
            animation-delay: -80s;
}


.row-container .row:nth-child(even) div:nth-child(2) {
    -webkit-animation: animate4 80s linear infinite;
            animation: animate4 80s linear infinite;
    -webkit-animation-delay: -40s;
            animation-delay: -40s;
}


@-webkit-keyframes animate3 {
    0% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    100% {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
}


@keyframes animate3 {
    0% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    100% {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
}


@-webkit-keyframes animate4 {
    0% {
        -webkit-transform: translateX(-200%);
                transform: translateX(-200%);
    }
    100% {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}


@keyframes animate4 {
    0% {
        -webkit-transform: translateX(-200%);
                transform: translateX(-200%);
    }
    100% {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}








