.info-container{
    padding: 3rem 1rem;
}

.info-wrapper{
    max-width: 70rem;
    padding: 0 5rem;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: space-around;
}

.info-back-btn{
    margin-bottom: 3rem;
    display: inline-block;
    color: var(--clr--pink-200);
    background: none;
    border : none;
    font-size: 1.2rem;
    cursor: pointer;
}

.info-content{
    display: flex;
    flex-direction: column;
}

.info-title{
    font-size: var(--fs-800);
    font-family: var(--ff-title);
    text-transform: capitalize;
    font-weight: var(--fw-semi-bold);
    margin-bottom: 0.8rem;
}

.info-subtitle{
    font-family: var(--ff-title);
    margin-bottom: 3rem;
}


/* 
/////////////
$   form
///////////////
 */

.info-form{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap:3rem;

    font-family: var(--ff-text);
    color: black;
    font-weight: var(--fw-regular);
    font-size: var(--fs-300);

}

.form-inputs{
    flex-shrink: 5;
}

.form-inputs input , .form-inputs textarea{
    margin-bottom: 1.5rem;
}

.info-form input , .info-form textarea{
    width: 100%;
}

.info-form input{
    height: 2.3rem;
    border: 1px solid rgb(167, 165, 165); 
}

.info-form textarea{
    min-height: 5rem;
    border: 1px solid rgb(167, 165, 165);
    resize: none;
}

/* 
/////////////
$    floating box
/////////////
 */
.info-floating-box{
    border: 1px solid rgb(167, 165, 165);
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;

    width: 300px;
    position: -webkit-sticky;
    position: sticky;
    top: 200px;
    scroll-behavior: smooth;
}

.floating-title{
    font-family: var(--ff-text);
    font-size: var(--fs-600);
    text-transform: capitalize;
    font-weight: var(--fw-med);
    margin-bottom: .1rem;
}

.info-price-hour{
    font-size: var(--fs-300);
    font-weight: var(--fw-med);
}

.info-floating-box hr{
    width: 100%;
    color:rgb(167, 165, 165);
}

.info-time{
    font-size: var(--fs-200);
    font-weight: var(--fw-med);
    padding: .3rem 0;
}


.submit{
    width: 100%;
    display: inline-block;
    padding: .7rem 2rem;
    border-radius: 0;
    border: none;
    background-color: var(--clr--pink-200);
    font-size: var(--fs-200);
}
.submit a{
    color: white;

}

.info-error{
    font-size: var(--fs-200);
    color: red;
}

